#root
{
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  user-select: none;
}

.k
{
  font-size: 5em;
  font-weight: bold;
  font-family: Arial, Helvetica, sans-serif;
  transition: transform 1s;
}

.k:hover
{
  transform: scale3d(1.5, 1.5, 1.5);
}